.custom-container {
  width: 1000px;
  height: 600px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  padding-left: 8px;
  margin: 8px 8px 0 0;
}

svg.custom :global(.node) circle {
  fill: #eee;
  stroke: #000;
  stroke-width: 1.5px;
}

svg.custom :global(.success-node) circle {
  fill: green;
  stroke: #000;
  stroke-width: 1.5px;
}

svg.custom :global(.active-node) circle {
  fill: green;
  stroke: blue;
  stroke-width: 3px;
}

svg.custom :global(.node) text {
  font-size: 12px;
  font-weight: 700;
  background-color: #444;
  fill: #222;
  text-shadow: 0 1px 4px #fff;
}

svg.custom :global(.node) {
  cursor: pointer;
}

svg.custom path:global(.link) {
  fill: none;
  stroke: #000;
  stroke-width: 1.5px;
}

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.tree {
}

.content {
  min-width: 400px;
  -webkit-box-flex: 500px;
      -ms-flex: 500px;
          flex: 500px;
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
}
