.modal-card {
  z-index: 10;
  margin: 0;
  padding: 2px 0 0 0;
  height: calc(100% - 60px);
} 

  .modal-card .content {
    height: calc(100% - 35px);
  } 

  .modal-card .card {
    -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, .2);
            box-shadow: 0px 0px 10px rgba(0, 0, 0, .2);
  }

.card-wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-width: 340px;
  height: 100%;
  margin: 6px 4px 0;
  position: relative;
  background: #fff;
}

.card {
  position: relative;
  height: 100%;
  border-radius: 0 !important;
  -webkit-box-shadow: rgba(0, 0, 0, .1) 0 0 1px 1px, rgba(0, 0, 0, .117647) 0 1px 6px, rgba(0, 0, 0, .117647) 0 1px 4px;
          box-shadow: rgba(0, 0, 0, .1) 0 0 1px 1px, rgba(0, 0, 0, .117647) 0 1px 6px, rgba(0, 0, 0, .117647) 0 1px 4px;
}

.content {
  height: calc(100% - 56px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.list-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
}

.isDragging {
  display: none;
}

.isOver {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  background: rgba(255, 255, 255, .75);
  border: 3px dashed #ddd;
  height: 100%;
  z-index: 1;
}

.loader {
  position: absolute;
  top: 70px;
  right: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 16px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.server-error {
  background: rgba(187, 0, 0, 0.4);
  padding: 8px;
}

.addButton {
  -ms-flex-item-align: start;
      align-self: flex-start;
  color: #666;
  font-size: 20px;
  padding: 8px;
}

.entryCount {
  position: absolute;
  display: inline-block;
  font-size: 13px;
  font-weight: 700;
  bottom: -12px;
  padding: 7px 6px 15px;
  color: rgb(255, 255, 255);
  background: rgba(0, 0, 0, .35);
  border-radius: 8px;
  right: 15px;
  border-top-right-radius: 0;
}
