.custom-container {
  background-color: #242424;
}

svg.custom :global(.node) circle {
  fill: #f3f3ff;
  stroke: #2593b8;
  stroke-width: 1.5px;
}

svg.custom :global(.node) text {
  font-size: 14px;
  background-color: #444;
  fill: #f4f4f4;
  text-shadow: 0 1px 4px black;
}

svg.custom :global(.node) {
  cursor: pointer;
}

svg.custom path:global(.link) {
  fill: none;
  stroke: #2593b8;
  stroke-width: 1.5px;
}

