.label {
  z-index: 1;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
  -webkit-transform-origin: left top 0px;
          transform-origin: left top 0px;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: rgba(0, 0, 0, .3);
}

.input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, .1);
}

.btnWrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center
}

.btnWrap input {
  border: none;
  padding: 0px;
  padding-top: 8px;
}

.btnWrap button {
  width: 100%;
  display: block;
  text-align: left;
}

.btnWrap span {
  display: block;
  line-height: 22px;
  padding-bottom: 2px;
}

.errorLine {
  border-bottom: 2px solid rgb(244, 67, 54);
}

.error {
  padding-top: 6px;
  color: rgb(244, 67, 54);
  font-size: 12px;
}

.wrapper {
  padding-top: 8px;
  padding-bottom: 16px;
}

