.general-info {
  position: relative;
  padding: 12px 12px 8px;
  line-height: 1.56;
}

.name {
  font-size: 18px;
  word-wrap: break-word;
  margin-right: 18px;
}

.id {
  font-size: 14px;
}

.date {
  font-size: 14px;
}

.email {
  font-size: 14px;
}

.openRevisions {
  font-size: 14px;
}

.revisions {
  margin: 0;
  list-style-type: none;
  background: #f1f1f1;
  padding: 4px 8px;
  border-radius: 3px;
}

.revision {
  font-size: 13px;
  font-style: italic;
  margin-bottom: 0;
}
