:global .nodeBase {
    cursor: pointer;
    fill: #777;
    stroke: #000;
    stroke-width: 2;
  }
  :global .leafNodeBase {
    cursor: pointer;
    fill: transparent;
    stroke: #000;
    stroke-width: 2;
  }
  :global .nodeNameBase {
    fill: #000;
    stroke: #000;
    stroke-width: 1;
  }
  :global .nodeAttributesBase {
    fill: #777;
    stroke: #777;
    stroke-width: 1;
    font-size: smaller;
  }
