:global .rd3t-tree-container {
    width: 100%;
    height: 100%;
  }
  :global .rd3t-grabbable {
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -webkit-grab;
  }
  :global .rd3t-grabbable:active {
    cursor: grabbing;
    cursor: -webkit-grabbing;
  }
