.sidebar {
  border-right: 2px solid #ddd;
  background: #f2f2f2;
  max-width: 280px;
  padding-top: 6px;
  position: relative;
  -webkit-transition: max-width 0.35s ease-in-out;
  transition: max-width 0.35s ease-in-out;
}

.collapsed {
  border-right: 2px solid #ddd;
  background: #f2f2f2;
  max-width: 56px;
  padding-top: 6px;
  -webkit-transition: max-width 0.35s ease-in-out;
  transition: max-width 0.35s ease-in-out;
}

.wrap {
  overflow: hidden;
}

.collapse-btn {
  position: absolute;
  bottom: 0;
  right: 0;
}

.open-btn {
  position: absolute;
  bottom: 0;
  right: 0
}

.open-btn svg {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
