.item {
  text-align: initial;
  color: inherit;
  background: #fff;
  height: 61px;
  margin-right: auto;
  width: 100%;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: left;
}

.multiedit-item {
  composes: item;
  padding-left: 24px;
}

.isOver {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  background: rgba(0, 0, 0, .02);
  border: 3px dashed #ddd;
  height: 100%;
  z-index: 1;
}

.data {
  padding: 8px 8px 12px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}

.dragHandle {
  cursor: move;
  -webkit-box-flex: 42px;
      -ms-flex: 42px;
          flex: 42px;
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
  -ms-flex-negative: 0.5;
      flex-shrink: 0.5;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 2px 0px 4px 6px;
  color: #616161;
  font-size: 14px
}

.dragHandle > div > svg {
  width: 22px !important;
  height: 22px !important;
}

.dragHandle > div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 1000px;
  background: #e0e0e0;
}

.item:hover {
  background: #f2f2f2;
}

.name {
  white-space: pre;
  font-size: 16px;
  text-align: left;
  margin-bottom: 4px;
}

.id {
  font-size: 14px;
  text-align: left;
}

.menu {
  text-align: left;
  position: absolute;
  right: 8px;
  bottom: 8px;
}

.delete {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  z-index: 10;
  position: absolute;
  right: 2px;
  top: 2px;
  opacity: 0.67;
}

.checkbox {
  position: absolute;
  top: 18px;
  margin-top: -8px;
  padding: 0;
  left: -8px;
}
